import React, { useState } from 'react';

const Contact=()=>{
    const[data,setData]=useState({
        fullname:'',
        phone:'',
        email:'',
        msg:'',
    });
    const InputEvent=(event)=>{
        const{name, value}= event.target;
        setData((preVal)=>{
            return{
                ...preVal,
                [name]:value,
            };
        });
    };
    const formSubmit=(e)=>{
        e.preventDefault();
        alert(`
        My name is ${data.fullname}
        My Phone number is ${data.phone}
        My email address is ${data.email}
        Message: ${data.msg}`);
    };
    return (
        <React.Fragment>
        <div className="my-5">
        <h1 className="text-center" style={{color:'#339933'}}><b>Contact Us</b></h1>
        </div>
        <div className="container contact_div">
        <div className="row">
        <div className="col-md-6 col-10 mx-auto">
        <form onSubmit={formSubmit}> 
    <div class="mb-3">
  <label for="exampleFormControlInput1" class="form-label">
  Full Name
  </label>
  <input 
  type="text" 
  class="form-control" 
  id="exampleFormControlInput1" 
  name="fullname"
  value={data.fullname}
  onChange={InputEvent}
  placeholder="Enter your name"/>
</div>
<div class="mb-3">
<label for="exampleFormControlInput1" class="form-label">
Phone Number
</label>
<input 
type="number" 
class="form-control" 
id="exampleFormControlInput1" 
name="phone"
value={data.phone}
onChange={InputEvent}
placeholder="Enter your mobile number"/>
</div>
<div class="mb-3">
<label for="exampleFormControlInput1" class="form-label">
Email address
</label>
<input 
type="email" 
class="form-control" 
id="exampleFormControlInput1" 
name="email"
value={data.email}
onChange={InputEvent}
placeholder="name@example.com"/>
</div>
<div class="mb-3">
  <label for="exampleFormControlTextarea1" class="form-label">
  Message
  </label>
  <textarea 
  class="form-control" 
  id="exampleFormControlTextarea1" 
  rows="3"
  name="msg"
  value={data.msg}
  onChange={InputEvent} >
  </textarea>
</div>
<div class="col-12 text-center">
<button class="btn btn-outline-success" type="submit">Submit</button>
</div>
        </form>
        </div>
        </div>
        </div>
        </React.Fragment>
    );
};

export default Contact;